@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
/* FONT = MONTSERRAT */
 
html{
  background: linear-gradient(110deg, rgba(44,62,80,1) 0%, rgba(0,0,0,1) 100%);
  height: 100vh;
}

html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  font-family: 'Montserrat', sans-serif;
}

body{
  background-color: transparent !important;
  scrollbar-color: #080b0e #4d4d4d;
  scrollbar-width: thin;
}

::-webkit-scrollbar{
  width: 12px;
  background-color:white;
}
::-webkit-scrollbar-track{
  background-color: black;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}
::-webkit-scrollbar-thum{
  background-color: white;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}



#root{
  width: 100%;
  height: 100%;
  padding-bottom: 5rem;
  overflow: hidden;
}

#topBar{list-style:none;margin:0;margin-top:1.5rem;padding:0;display:flex;justify-content:center;align-items:center;text-transform:uppercase;font-size:.8rem;font-weight:700;color:#fff}#topBar li{margin:0 .5rem;display:flex;flex-direction:column;align-items:center;cursor:pointer}#topBar li a{margin-bottom:2px;color:#fff;text-decoration:none}#topBar li span{content:" ";border-bottom:2px solid limegreen;transition:width 1s ease;-webkit-transition:width 1s ease;-moz-transition:width 1s ease;-ms-transition:width 1s ease;-o-transition:width 1s ease;width:'1px';visibility:hidden}#topBar li:hover span{width:100%;visibility:visible;transition:width 300ms ease;-webkit-transition:width 300ms ease;-moz-transition:width 300ms ease;-ms-transition:width 300ms ease;-o-transition:width 300ms ease}

.strivify-card{width:175px;height:175px;background-size:cover;margin-right:1rem;position:relative}.strivify-card .strivify-card-header{position:absolute;font-weight:700}.strivify-card .strivify-card-header p{margin:0}

#trending{width:100%;padding:2rem}#trending .top-hits,#trending .top-50{color:#fff}#trending .top-hits h2,#trending .top-50 h2{font-size:3rem;text-transform:uppercase;font-weight:900}#trending .top-hits .row,#trending .top-50 .row{margin:0}#trending .top-hits{margin-bottom:2rem}

#podcast{width:100%;padding:2rem;text-align:center;color:white}#podcast .fa-satellite-dish{font-size:5rem;margin-bottom:1rem}#podcast h2{font-weight:800;font-size:2.5rem}#podcast p:nth-of-type(1){margin-bottom:0}#podcast p:nth-of-type(2){font-size:.85rem;color:rgba(255,0,0,0.8);margin-bottom:1.5rem}#podcast button{border-radius:9999px;-webkit-border-radius:9999px;-moz-border-radius:9999px;-ms-border-radius:9999px;-o-border-radius:9999px;border:none;padding:5px 10px;font-size:.9rem;font-weight:700;filter:brightness(80%);-webkit-filter:brightness(80%);padding:1rem 2rem;font-size:1.5rem;font-weight:800}#podcast button:hover{filter:brightness(100%);-webkit-filter:brightness(100%)}

#moods-genres{width:100%;padding:2rem}#moods-genres .moods,#moods-genres .genres{color:#fff}#moods-genres .moods h2,#moods-genres .genres h2{font-size:3rem;text-transform:uppercase;font-weight:900}#moods-genres .moods .row,#moods-genres .genres .row{margin:0}#moods-genres .moods{margin-bottom:2rem}

#new-releases{width:100%;padding:2rem;color:#fff}#new-releases h2{font-size:3rem;text-transform:uppercase;font-weight:900}#new-releases .row{margin:0;padding:0}#new-releases .row .strivify-card{border-radius:50%;-webkit-border-radius:50%;-moz-border-radius:50%;-ms-border-radius:50%;-o-border-radius:50%;margin-left:2rem}#new-releases .row .strivify-card .strivify-card-header{margin-bottom:-.5rem;text-align:center;width:100%}#new-releases .row .strivify-card .strivify-card-header p:nth-of-type(2){font-size:.85rem;font-weight:500}

#home-page{transition:all 1s ease;-webkit-transition:all 1s ease;-moz-transition:all 1s ease;-ms-transition:all 1s ease;-o-transition:all 1s ease;height:100%;overflow-x:hidden;overflow-y:auto}

#login-page{width:100%;height:100vh;background-color:#0d0d0d}#login-page .container{padding-top:3rem;display:flex;flex-direction:column;align-items:center}#login-page .container header{display:flex;justify-content:center;align-items:center;margin-bottom:2rem}#login-page .container header img{width:100px;height:100px}#login-page .container header span{font-size:3rem;color:white;font-weight:bold}#login-page .container #login-fb{border-radius:9999px;-webkit-border-radius:9999px;-moz-border-radius:9999px;-ms-border-radius:9999px;-o-border-radius:9999px;border:none;padding:5px 10px;font-size:.9rem;font-weight:700;filter:brightness(80%);-webkit-filter:brightness(80%);min-width:300px;color:white;background-color:#3b5998;margin-bottom:.5rem}#login-page .container #login-fb:hover{filter:brightness(100%);-webkit-filter:brightness(100%)}#login-page .container .separator{display:flex;align-items:center;text-align:center;min-width:300px;text-transform:uppercase;font-size:.9rem;font-weight:600;color:white;margin-bottom:.5rem}#login-page .container .separator::before,#login-page .container .separator::after{content:" ";flex:1 1;border-bottom:1px solid}#login-page .container .separator::before{margin-right:.5rem}#login-page .container .separator::after{margin-left:.5rem}#login-page .container .separator::before,#login-page .container .separator::after{border-color:white}#login-page .container input{min-width:300px;border-radius:5px;-webkit-border-radius:5px;-moz-border-radius:5px;-ms-border-radius:5px;-o-border-radius:5px;border:none;padding:5px 1rem;font-size:.9rem;font-weight:600;margin-bottom:.75rem;outline:none;background-color:#282828;color:white}#login-page .container p{margin-top:-.5rem;margin-bottom:.5rem;font-size:.8rem;color:rgba(255,255,255,0.5);text-decoration:underline;cursor:pointer}#login-page .container p:hover{color:#fff}#login-page .container .remember{min-width:300px;display:flex;justify-content:space-between;color:white;font-weight:600;margin-bottom:1rem;position:relative}#login-page .container .remember .toast{top:-4rem;right:-90%}#login-page .container #login{border-radius:9999px;-webkit-border-radius:9999px;-moz-border-radius:9999px;-ms-border-radius:9999px;-o-border-radius:9999px;border:none;padding:5px 10px;font-size:.9rem;font-weight:700;filter:brightness(80%);-webkit-filter:brightness(80%);background-color:#00d95f;color:white;min-width:150px}#login-page .container #login:hover{filter:brightness(100%);-webkit-filter:brightness(100%)}

.toast{border-radius:5px;-webkit-border-radius:5px;-moz-border-radius:5px;-ms-border-radius:5px;-o-border-radius:5px;position:absolute;border:white 1px solid;z-index:9999;-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);background-color:rgba(0,0,0,0.6)}.toast .toast-header{background-color:inherit}.toast .toast-header button{border:none;background-color:transparent;font-weight:bold;color:white}.toast .toast-header button.close{display:none}.toast .toast-body{font-size:.6rem}.toggle-btn{width:50px;height:25px;border-radius:9999px;-webkit-border-radius:9999px;-moz-border-radius:9999px;-ms-border-radius:9999px;-o-border-radius:9999px;border:1px solid}.toggle-btn div{width:23px;height:23px;border-radius:50%;-webkit-border-radius:50%;-moz-border-radius:50%;-ms-border-radius:50%;-o-border-radius:50%;transition:margin-left 1s ease;-webkit-transition:margin-left 1s ease;-moz-transition:margin-left 1s ease;-ms-transition:margin-left 1s ease;-o-transition:margin-left 1s ease}.toggle-btn .toggle{background-color:white;cursor:pointer}.logo{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);-webkit-transform:translate(-50%, -50%);-moz-transform:translate(-50%, -50%);-ms-transform:translate(-50%, -50%);-o-transform:translate(-50%, -50%)}.logo img{animation:scaleLogo 2s ease 1;-webkit-animation:scaleLogo 2s ease 1;transform-origin:50% 50%}@keyframes scaleLogo{0%{}100%{transform:scale(0.1);-webkit-transform:scale(0.1);-moz-transform:scale(0.1);-ms-transform:scale(0.1);-o-transform:scale(0.1)}}.show-filters{margin-top:-.5rem;font-size:.75rem;text-decoration:underline;cursor:pointer}.show-filters:hover{color:white}.filters div{display:flex;align-items:center}.filters div input{margin-left:.5rem;margin-right:.5rem}.filters div label{margin:0;margin-right:1rem}

#sidebar{max-width:250px;height:calc(100vh - 5rem);padding:1rem;background-color:black;display:flex;flex-direction:column;align-items:center;position:fixed;top:0;left:0;color:rgba(255,255,255,0.7);z-index:9999;transition:margin-left 1s ease;-webkit-transition:margin-left 1s ease;-moz-transition:margin-left 1s ease;-ms-transition:margin-left 1s ease;-o-transition:margin-left 1s ease}#sidebar .toggle{content:" ";width:20px;height:100px;position:absolute;right:-20px;top:50%;transform:translate(0, -50%);-webkit-transform:translate(0, -50%);-moz-transform:translate(0, -50%);-ms-transform:translate(0, -50%);-o-transform:translate(0, -50%);background-color:inherit;border-top-right-radius:9999px;border-bottom-right-radius:9999px}#sidebar .logo-sidebar{display:flex;align-items:center;margin-bottom:1rem}#sidebar .logo-sidebar img{width:50px;height:50px}#sidebar .logo-sidebar span{font-size:2.5rem;font-weight:800;margin-left:.5rem;color:#1ed760}#sidebar input[type=text]{min-width:90%;border-radius:5px;-webkit-border-radius:5px;-moz-border-radius:5px;-ms-border-radius:5px;-o-border-radius:5px;border:none;padding:5px 1rem;font-size:.9rem;font-weight:600;margin-bottom:.75rem;outline:none;max-width:90%}#sidebar .menu{height:60vh;display:flex;flex-direction:column;align-items:center;justify-content:space-between}#sidebar .menu ul{list-style:none;margin:0;padding:0}#sidebar .menu ul #create-playlist{padding:0.5rem;overflow:hidden}#sidebar .menu ul #create-playlist input{width:100%;margin:0;margin-right:auto;background-color:transparent;color:white;border-bottom:1px solid white;border-radius:0px;-webkit-border-radius:0px;-moz-border-radius:0px;-ms-border-radius:0px;-o-border-radius:0px}#sidebar .menu ul li{width:100%;padding:.5rem;display:flex;align-items:center;font-size:1rem;font-weight:600;cursor:pointer}#sidebar .menu ul li:hover{color:white}#sidebar .menu ul li i,#sidebar .menu ul li ion-icon{font-size:1.5rem;margin-right:.5rem}#sidebar .menu ul li a{color:rgba(255,255,255,0.7);display:flex;align-items:center;text-decoration:none}#sidebar .menu ul li a:hover{color:white}#sidebar .menu .install{width:100%;text-align:center}#sidebar .menu .install i{margin-right:.5rem}#sidebar .menu .install span{font-size:.9rem;text-transform:uppercase;font-weight:700}#sidebar .menu .install hr{background-color:rgba(255,255,255,0.7)}#sidebar .login{width:100%;display:flex;flex-direction:column;align-items:center;justify-content:space-evenly}#sidebar .login .user{width:100%;margin-bottom:.5rem;display:flex;align-items:center;justify-content:space-evenly}#sidebar .login .user img{border-radius:50%;-webkit-border-radius:50%;-moz-border-radius:50%;-ms-border-radius:50%;-o-border-radius:50%;width:30px;height:30px}#sidebar .login .user p{margin:0;font-size:1.25rem;font-weight:800;color:#fff}#sidebar .login button{border-radius:9999px;-webkit-border-radius:9999px;-moz-border-radius:9999px;-ms-border-radius:9999px;-o-border-radius:9999px;border:none;padding:5px 10px;font-size:.9rem;font-weight:700;filter:brightness(80%);-webkit-filter:brightness(80%);width:125px;background-color:transparent;color:white;border:2px solid #1ed760}#sidebar .login button:hover{filter:brightness(100%);-webkit-filter:brightness(100%)}#sidebar .login button a{color:white;text-decoration:none}

.player-container{position:fixed;bottom:0;width:100%;height:5rem;z-index:9999}.player-container #player{width:100%;display:flex;align-items:center}


